$alert-padding-y: 0.75rem !default;
$alert-padding-x: 1.25rem !default;
$alert-margin-bottom: 1rem !default;
$alert-border-width: 1px !default;
$alert-border-radius: 0.25rem !default;
$alert-description-font-size: 1rem !default;
$alert-description-top-margin: 0.3rem !default;

$alert-colors: (
  'default': $primary,
  'success': $success,
  'warning': $warning,
  'danger': $danger
);
.v-alert {
  position: relative;
  padding: $alert-padding-y $alert-padding-x;
  margin-bottom: $alert-margin-bottom;
  border: $alert-border-width solid transparent;
  border-radius: $alert-border-radius;
  .v-alert-close {
    position: absolute;
    top: 0;
    right: 0;
    padding: $alert-padding-y $alert-padding-x;
    color: inherit;
    cursor: pointer;
  }
  .bold-title {
    font-weight: $font-weight-bold;
  }
  .v-alert-desc {
    font-size: $alert-description-font-size;
    margin: $alert-description-top-margin 0 0;
  }
}
@each $color, $value in $alert-colors {
  .v-alert-#{$color} {
    @include alert-style($value, darken($value, 5%), $white);
  }
}
